.container {
  margin: auto 277px;
}

@media only screen and (max-width: 1565px) {
  .container {
    margin: auto 150px;
  }
}
@media only screen and (max-width: 768px) {
  .container {
    margin: auto 25px;
  }
}
@media only screen and (max-width: 575px) {
  .container {
    margin: auto 20px;
  }
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-weight: 400;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
}

img,
picture,
svg {
  max-width: 100%;
  display: block;
}

@media (prefers-reduced-motion: reduce) {
  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
h1 {
  font-size: 96px;
  color: #4c12a1;
  font-weight: 400;
}

h2 {
  font-size: 42px;
  color: #4c12a1;
  font-weight: 400;
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 50px;
    color: #4c12a1;
  }
  h2 {
    font-size: 38px;
    color: #4c12a1;
  }
}
@media screen and (max-width: 575px) {
  h1 {
    font-size: 32px;
    color: #4c12a1;
  }
  h2 {
    font-size: 24px;
    color: #4c12a1;
  }
  h3 {
    font-size: 20px;
    color: #4c12a1;
  }
}
@media only screen and (max-width: 575px) {
  .banner {
    width: 100%;
    height: 415px;
    background-image: url(/img/desktop-large.jpg);
    background-position: center bottom;
  }
  .banner__content {
    padding: 65px 0 0 0;
  }
  .banner__heading {
    max-width: 280px;
    margin-bottom: 35px;
  }
}
@media only screen and (max-width: 768px) {
  .banner {
    background-image: url(/img/deskto-medium.png);
  }
}

.investor {
  background-color: #cae7e6;
}
@media only screen and (max-width: 575px) {
  .investor__content {
    padding: 28px 0 29px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .investor__title {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .investor__heading {
    width: 279px;
    margin-bottom: 15px;
  }
  .investor__paragraph {
    text-align: center;
    width: 280px;
    margin-bottom: 7px;
    font-size: 14px;
  }
  .investor__number {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .investor__num {
    font-size: 32px;
  }
  .investor__text {
    text-align: center;
    margin-bottom: 20px;
    font-size: 17px;
    font-weight: 700;
  }
  .investor__line {
    margin-bottom: 20px;
    border: 1px solid #000;
  }
  .investor__cbs {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 28px;
  }
  .investor__country {
    font-size: 14px;
    font-weight: 700;
  }
  .investor__button {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 575px) {
  .sustainability {
    background-image: url(/img/sustainability-small.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .sustainability__content {
    padding: 38px 0 78px 0;
  }
  .sustainability__title {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .sustainability__span {
    padding: 5px 15px;
    margin-bottom: 20px;
    border-radius: 5px;
    color: #fff;
    background: #56707a;
    font-weight: 900;
  }
  .sustainability__heading {
    width: 282px;
    text-align: right;
    margin-bottom: 25px;
    color: #fff;
  }
  .sustainability__button {
    margin-bottom: 35px;
  }
  .sustainability__line {
    margin-bottom: 20px;
    border: 1px solid #fff;
  }
  .sustainability__number {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .sustainability__num {
    font-size: 30px;
    color: #fff;
  }
  .sustainability__text {
    width: 185px;
    margin-bottom: 25px;
    text-align: right;
    font-size: 20px;
    color: #fff;
  }
  .sustainability__arrows {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .sustainability__arrow {
    display: flex;
    align-items: center;
    padding: 5px 25px;
    border-radius: 25px;
    cursor: pointer;
    border: 2px solid #cdd8df;
    background-color: rgba(255, 255, 255, 0.4);
  }
  .sustainability__arrow-icon {
    width: 25px;
    height: 25px;
    color: #4c12a1;
  }
}

@media only screen and (max-width: 1280px) {
  .rectangle {
    display: none;
  }
}
@media only screen and (max-width: 575px) {
  .rectangle {
    display: none;
  }
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__list {
  display: grid;
  grid-template-columns: repeat(7, minmax(min-content, max-content));
  gap: clamp(20px, 2vw, 40px);
  list-style: none;
}
.header__link {
  font-size: clamp(16px, 1vw, 18px);
  text-decoration: none;
  color: #213038;
}
.header__number {
  display: none;
}
.header__line {
  display: none;
  border: 1px solid #000;
  height: 16px;
  margin: 0 10px;
}
.header__icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.header__icon--open {
  display: none;
}
.header__icon--close {
  display: none;
}
@media only screen and (max-width: 1565px) {
  .header__nav {
    display: none;
  }
}
@media only screen and (max-width: 1280px) {
  .header {
    padding: 25px 0;
  }
  .header__content {
    justify-content: flex-end;
  }
  .header__logo {
    margin-right: auto;
  }
  .header__number {
    display: block;
  }
  .header__line {
    display: block;
  }
  .header__icon--open {
    display: block;
  }
  .header .active {
    display: block;
  }
}
@media only screen and (max-width: 575px) {
  .header {
    position: relative;
    padding: 20px 0;
  }
  .header__nav {
    display: none;
    position: absolute;
    height: 100vh;
    top: 85px;
    left: 0;
    width: 100%;
    padding: 20px;
    background-color: #fff;
  }
  .header__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
  }
  .header__number {
    display: none;
  }
  .header__line {
    display: none;
  }
  .header__icon--open {
    display: block;
  }
}

@media only screen and (max-width: 575px) {
  .brand__content {
    padding: 40px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .brand__title {
    width: 274px;
    margin-bottom: 35px;
  }
  .brand__heading {
    margin-bottom: 35px;
  }
  .brand__line {
    border: 1px solid #cdd8df;
    margin-bottom: 35px;
  }
  .brand__arrows {
    display: flex;
    align-items: center;
    gap: 15px;
  }
  .brand__arrow {
    display: flex;
    align-items: center;
    padding: 5px 25px;
    border-radius: 25px;
    cursor: pointer;
    border: 2px solid #cdd8df;
  }
  .brand__icon {
    width: 25px;
    height: 25px;
    color: #4c12a1;
  }
  .brand__cards {
    position: relative;
  }
  .brand__card {
    width: 274px;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    margin-bottom: 42px;
    border-radius: 10px;
  }
  .brand__img {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  .brand__releaseDate {
    display: flex;
    left: -20px;
    top: 260px;
    position: absolute;
  }
  .brand__release {
    font-size: 11px;
    font-weight: 900;
    padding: 5px 12px 5px 12px;
    border-radius: 5px;
    background-color: #4c12a1;
    color: #fff;
  }
  .brand__date {
    font-size: 11px;
    font-weight: 900;
    padding: 5px 12px 5px 12px;
    border-radius: 5px;
    margin-left: -5px;
    background-color: #fff;
    color: #56707a;
    border: 1px solid #4c12a1;
  }
  .brand__text {
    padding: 25px 25px;
    font-size: 20px;
    color: #e5006d;
  }
  .brand__underCard {
    display: flex;
    align-items: center;
  }
  .brand__buttonLine {
    width: 42px;
    height: 1px;
    border: 1px solid #cdd8df;
  }
}

@media only screen and (max-width: 575px) {
  .cta__content {
    padding: 48px 0 61px 0;
    display: flex;
    justify-content: center;
  }
  .cta__card {
    max-width: 280px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius: 10px;
  }
  .cta__img {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    margin-bottom: 15px;
  }
  .cta__description {
    padding: 0 20px;
  }
  .cta__heading {
    margin-bottom: 12px;
  }
  .cta__text {
    font-size: 18px;
    width: 250px;
    margin-bottom: 15px;
    color: #213038;
  }
  .cta__textBottom {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 25px;
    gap: 10px;
  }
  .cta__textBottom-line {
    width: 35px;
    border: 1px solid #e5006d;
  }
  .cta__textBottom-text {
    font-size: 16px;
    color: #4c12a1;
    font-weight: 700;
  }
}

@media only screen and (max-width: 575px) {
  .carrers {
    background-image: url(/img/cta-small.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .carrers__content {
    padding: 100px 0 100px 0;
  }
  .carrers__title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .carrers__subtitle {
    background-color: #56707a;
    font-weight: 700;
    color: #fff;
    padding: 5px 15px;
    border-radius: 5px;
    margin-bottom: 55px;
  }
  .carrers__heading {
    width: 280px;
    color: #fff;
    font-size: 24px;
    margin-bottom: 35px;
  }
}

@media only screen and (max-width: 575px) {
  .footer__content {
    padding: 56px 0 50px 0;
  }
  .footer__action {
    margin-bottom: 59px;
  }
  .footer__logo {
    margin-bottom: 40px;
  }
  .footer__column:not(:last-child) {
    margin-bottom: 50px;
  }
  .footer__heading {
    font-weight: 700;
    margin-bottom: 20px;
  }
  .footer__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    list-style: none;
  }
  .footer__link {
    text-decoration: none;
    font-size: 16px;
    color: #213038;
  }
}

@media only screen and (max-width: 575px) {
  .contact {
    background-color: #f2f3f4;
  }
  .contact__content {
    padding: 28px 0 53px 0;
  }
  .contact__solutions {
    margin-bottom: 45px;
  }
  .contact__list {
    list-style: none;
  }
  .contact__link {
    font-size: 14px;
    text-decoration: none;
    color: #56707a;
  }
  .contact__text {
    font-size: 16px;
    color: #56707a;
  }
}

.btn {
  display: block;
  padding: 11px 48px 15px 48px;
  font-size: 16px;
  background-color: #4c12a1;
  color: #fff;
  border: none;
  border-radius: 25px;
  cursor: pointer;
}

@media only screen and (max-width: 1565px) {
  .header__button {
    display: none;
  }
}